@use 'global' as *;

// ブログカード

.blog-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(rem(330), 1fr));
  column-gap: rem(40);
  row-gap: rem(39);
  @include mq('md') {
    grid-template-columns: repeat(2, 1fr);
    row-gap: rem(80);
  }
}
.blog-cards.blog-cards--top {
  @include mq('md') {
    grid-template-columns: repeat(3, 1fr);
  }
}
.blog-cards__item {
  box-shadow: rem(2) rem(2) rem(10) 0 rgba(0, 0, 0, 0.25);
  background-color: $white;
  width: 96.52%;
  margin-right: auto;
  margin-left: auto;

  @include mq('md') {
    width: 100%;
  }
}

.blog-cards__item a {
  display: block;
  padding: rem(16) rem(16) rem(24) rem(16);
  height: 100%;
  transition: opacity 0.3s;
  @include mq('md') {
    padding: rem(16) rem(19) rem(25) rem(16);
  }
}
.blog-cards__item a:hover {
  opacity: 0.7;
}
.blog-cards__img {
  overflow: hidden;
}
.blog-cards__img img {
  aspect-ratio: 301 / 201;
  object-fit: cover;
  width: 100%;
  transition: transform 0.6s ease;
  overflow: hidden;

  @include mq('md') {
  }
}
.blog-cards__img:hover img {
}
.blog-cards__item:hover img {
  transform: scale(1.3);
}
.blog-cards__body {
  margin-top: rem(19);
  @include mq('md') {
  }
}

.blog-cards__date {
  font-family: $base-font-family;
  font-size: rem(12);
  line-height: 1.5;
  font-weight: $regular;
  color: $darkGreen;
  @include mq('md') {
  }
}

.blog-cards__title {
  font-family: $third-font-family;
  border-bottom: rem(1) solid $lightGreen;
  font-size: rem(16);
  line-height: 1.5;
  font-weight: $medium;
  color: $darkGreen;
  padding-bottom: rem(3);

  @include mq('md') {
  }
}
.blog-cards__text {
  font-family: $third-font-family;
  font-size: rem(16);
  line-height: 1.5;
  font-weight: $regular;
  color: $darkGreen;
  margin-top: rem(16);
  @include mq('md') {
  }
}
